import Vue from 'vue';
import Router from 'vue-router';
import Hello from '../components/Hello';
import Satings from '../components/satings';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/hello',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/satings',
      name: 'satings',
      component: Satings
    }
  ]
});



